.parent-column{
    margin: 2px 10px;
    margin-bottom: 40px;
    display: flex;
    justify-content: center;
    background-color: #151A1D;
    height:80vh;
}

.main-component {

    justify-content: center;
    display: inline-block;
    width: 75%;

}

.download-cv-small {
    display: none;
}

.MuiButton-outlinedPrimary {
    color: aqua !important;
    border: 1px solid aqua !important;
}
.MuiButton-outlinedPrimary:hover {
    transform: scale(1.1);
    color : "#88F9FC"
}


.column1{
    display: inline-block;
    width: 15%;
    margin-top: 50px;
    text-align: center;

}

.download-cv {
    margin-top : 40px;
    text-align: center;
}

.column3{
    display: inline-block;
    width: 15%;
    margin-top: 50px;
    text-align: center;
}

ul {
    list-style-type: none;
    padding: 0px;
    margin: 0px;
}

.profile-summary {
    flex-wrap: nowrap;
    height: 85vh;
    padding: 10px;
    margin-top: 25px;
    margin-bottom: 30px;
    font-size: large;
}
.profile-picture-name-designation-section {
    margin-top: 40px;
    width: 100%;
    display: flex;
    flex-direction: row;
}
.profile-picture-section {
    margin: 0 50px;
    flex-grow: 1;
    flex-shrink: 3;
}

.profile-name-designation-section {
    flex-grow: 8;
    flex-direction: column;
    position: relative;
}
.profile-name-section {
    margin-top: 100px;
    flex-grow: 2;
    height: 25%;
    color: beige;
    font-size: 80px;
    flex-direction: row;
    flex-wrap: wrap;
    display: flex;
}
@keyframes slideFromTop {
    0% {
      transform: translateY(-200%);
    }
  }
@keyframes slideFromBottom {
0% {
    transform: translateY(200%);
}
}
.profile-name-first{flex-grow: 0.1; margin-right: 10px;  animation: 1s ease-out 0s 1 slideFromTop; }
.profile-name-middle{flex-grow: 0.1; margin-right: 10px;  animation: 1s ease-out 0s 1 slideFromBottom;}
.profile-name-last {flex-grow: 0.1; margin-right: 10px;  animation: 1s ease-out 0s 1 slideFromTop;}
.profile-name-first:hover , .profile-name-middle:hover , .profile-name-last:hover { color: rgb(144, 129, 129); }

.profile-designation-section {
    flex-grow: 2;
    height: 15%;
    color: white;
    font-size: 30px;
}

.profile-brief-section {
    margin-top: 10px;
    width: 65%;;
    color: #58585f;
    font-size: x-large;
}

@keyframes slideInFromLeft {
    0% {
      transform: translateX(-200%);
    }
  }

.profile-picture {
    margin-top: 90px;
    border-radius: 5000px;
    height: 30vh !important;
    border: 1px solid aqua;
    animation: 1s ease-out 0s 1 slideInFromLeft;

}
.profile-picture:hover { border : 2px solid aqua }

@media all and (max-width : 875px) {
    .profile-summary {
        margin-top: 10px;
        font-size: small;
        padding: 0 10px 0 10px;

    }

    .profile-picture-section {
        margin: 0px;
        flex-grow: 1;
        flex-shrink: 3;
    }
    .profile-picture-section{
        display: none;
        height: 10vh !important;
        /* border-radius: 2000px;    */
        margin-bottom: 5px;
    }
    .download-cv-small {
        display: block;
        margin-top: 50px;
    }
    .profile-name-section {
        margin-top: 100px;
        flex-grow: 2;
        height: 35%;
        color: beige;
        font-size: 50px;
    }
}

@media all and (max-width : 1600px) {
    .profile-picture{
        /* display: none; */
        height: 30vh !important;
        border-radius: 2000px;   
        margin-bottom: 5px;
    }
    .profile-name-section {
        margin-top: 60px;
        flex-grow: 2;
        height: 35%;
        color: beige;
        font-size: 60px;
    }

    .profile-designation-section {
        flex-grow: 2;
        height: 13%;
        color: white;
        font-size: 30px;
    }

    .profile-brief-section {
        /* margin-top: 10px; */
        width: 65%;;
        color: #58585f;
        font-size: x-large;
    }
}

@media all and (max-width: 680px) {
    .profile-picture-name-designation-section {
        margin-top: 5px;
    }
    .profile-name-section {
        height: 20%;
        margin-top: 30px
    }
.profile-name-first,.profile-name-middle, .profile-name-last {font-size: 35px; }
    .profile-designation-section {
        margin: 15px 0;
        font-size: 20px;
        height: 5%;
    }
    .profile-brief-section {
        /* margin-top: 15px; */
        width: 70%;
    }
    .download-cv {
        margin-top : 40px;
        text-align: center;
    }
}